<template>
  <div>
    <Head title="地址编辑"></Head>
    <van-address-edit
      :area-list="areaList"
      show-postal
      show-delete
      show-set-default
      :address-info="AddressInfo"
      show-search-result
      :search-result="searchResult"
      @change-area="getAdsMsg"
      @save="onSave"
      @delete="onDelete"
      @change-detail="onChangeDetail"
    />
  </div>
</template>

<script>
import Head from "@/components/Head.vue";
import { areaLists } from "@/comment/areaList.js";
import { mapState } from "vuex";
export default {
  // props:["searchResult"],
  computed: {
    ...mapState(["AddressInfo"])
  },
  data() {
    return {
      areaList: areaLists,
      searchResult: [],
      areaCode: ""
    };
  },
  components: {
    Head
  },
  methods: {
    getAdsMsg(values) {
      this.areaCode = values[2].code;
    },
    onSave(content) {
      let address =
        content.province +
        content.city +
        content.county +
        content.addressDetail;
      let _id = "";
      const username = JSON.parse(sessionStorage.userInfo).username;
      if (this.$route.params.adsNow) {
        _id = this.$route.params.adsNow;
      }
      this.$axios
        .post("/vue/saveAds", {
          addressName: content.name,
          addressTel: content.tel,
          addressAds: address,
          postCode: content.postalCode,
          province: content.province,
          city: content.city,
          county: content.county,
          addressDetail: content.addressDetail,
          isDefault: content.isDefault,
          areaCode: this.areaCode,
          _id,
          username,
        })
        .then(res => {
          this.$router.push({name:"chooseads"})
        });
    },
    onDelete(content) {
      let address =
        content.province +
        content.city +
        content.county +
        content.addressDetail;
      const username = JSON.parse(sessionStorage.userInfo).username;
      this.$axios
        .post("/vue/delAds", {
          addressName: content.name,
          addressTel: content.tel,
          addressAds: address,
          postCode: content.postalCode,
          username,
        })
        .then(res => {
          this.$router.push({ name: "chooseads"});
        });
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "金融港B18栋",
            address: "武汉市江夏区"
          }
        ];
      } else {
        this.searchResult = [];
      }
    }
  }
};
</script>

